.comment-section {
    margin-top: 20px;
}

.comment-section-title {
    margin-bottom: 10px;
}

.comment-input-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.comment-input {
    flex: 1;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
    margin-right: 8px;
}

.comment-save-button {
    padding: 8px 16px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.comment-list {
    margin-top: 10px;
}

.comment-item {
    margin-bottom: 5px;
}

.comment-placeholder {
    color: #999;
    font-style: italic;
}

.comment-section {
    margin-top: 20px;
}

.comment-section-title {
    margin-bottom: 10px;
    font-size: 20px;
}

.comment-input-container {
    display: flex;
    align-items: center;
}

.comment-input {
    flex: 1;
    padding: 5px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
}

.comment-save-button {
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    background-color: #313334;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
}

.comment-list {
    margin-top: 10px;
}

.comment-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.comment-text {
    flex: 1;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
}

.comment-actions {
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.comment-edit-button,
.comment-delete-button {
    padding: 3px 8px;
    margin-right: 5px;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    cursor: pointer;
}

.comment-edit-button {
    background-color: #726ce2;
    color: #fff;
}

.comment-delete-button {
    background-color: #f65565;
    color: #fff;
}